.actions-container {
  position: relative;
  display: inline-block;
  font-family: 'Poppins', sans-serif;
}

.actions-trigger {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 6px;
  color: #6b7280;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.actions-trigger:hover {
  background-color: #f3f4f6;
  color: #374151;
}

.actions-trigger.active {
  background-color: #e5e7eb;
  color: #374151;
}

.actions-trigger svg {
  width: 16px;
  height: 16px;
}

.actions-menu {
  position: absolute;
  bottom: 100%;
  right: 0;
  margin-block-end: 0.5rem;
  background: white;
  border-radius: 8px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
  border: 1px solid #e2e8f0;
  min-width: 200px;
  z-index: 9999;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease;
  transform: translateY(10px);
  backdrop-filter: blur(10px);
}

.actions-menu.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.actions-menu.rtl {
  right: auto;
  left: 0;
}

/* Add arrow indicator pointing to the trigger */
.actions-menu::after {
  content: '';
  position: absolute;
  top: 100%;
  right: 1rem;
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid white;
  filter: drop-shadow(0 2px 2px rgba(0, 0, 0, 0.1));
}

.actions-menu.rtl::after {
  right: auto;
  left: 1rem;
}

.action-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  width: 100%;
  padding: 0.75rem 1rem;
  background: none;
  border: none;
  font-weight: 600;
  text-align: start;
  cursor: pointer;
  transition: background-color 0.2s ease;
  color: #374151;
  font-size: 0.9rem;
  font-family: 'Poppins', sans-serif;
}

.action-item:hover {
  background-color: #f3f4f6;
}

.action-item:first-child {
  border-radius: 8px 8px 0 0;
}

.action-item:last-child {
  border-radius: 0 0 8px 8px;
}

.action-item.action-danger {
  color: #dc2626;
}

.action-item.action-danger:hover {
  background-color: #fef2f2;
}

.action-icon {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}

.action-divider {
  height: 1px;
  background-color: #e5e7eb;
  margin: 0.5rem 0;
}

/* RTL Support */
.actions-container.rtl .actions-menu {
  right: auto;
  left: 0;
}

.actions-container.rtl .action-item {
  text-align: right;
}

/* Responsive Design */
@media (max-width: 768px) {
  .actions-menu {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    max-width: 300px;
    right: auto;
    left: auto;
    bottom: auto;
    margin-block-end: 0;
  }
  
  .actions-menu.show {
    transform: translate(-50%, -50%);
  }
  
  .actions-menu.rtl {
    right: auto;
    left: auto;
  }
}

/* Animation */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.actions-menu.show {
  animation: slideIn 0.2s ease-out;
}
